.item {
    padding: 10px 0;
}
.item .ant-checkbox-wrapper {
    margin-right: 20px;
}
.item.done  .ant-checkbox-checked .ant-checkbox-inner{
    background-color: #f5f5f5;
    border-color: #d9d9d9!important;
}

.item.done  .ant-checkbox-checked .ant-checkbox-inner::after {
    border-color: rgba(0,0,0,.25);
}

.item.done .ant-checkbox:hover::after{
    visibility: hidden;
}
.item.done {
    color: #666;
    text-decoration: line-through;
}
.item .anticon {
    float: right;
    cursor: pointer;

}